<template>
	<div>
		<b-modal size="sm" v-model="showModal" :ok-disabled="!noteText" @ok="saveNote(cardTitle, noteText)">
			<div slot="modal-title">
				<div class="input-group mb-3">
					<div class="d-flex">
						<h4 v-if="newCardHeader" class="mr-4 ml-auto mt-auto mb-auto">
							Новая заметка
						</h4>
						<button class="btn btn-outline-secondary btn-sm m-0" type="button" v-if="newCardHeader" @click="newCardHeader = false"><i class="fas fa-pencil-alt"></i></button>
					</div>
					<input type="text" class="form-control" placeholder="Название" aria-label="Название" aria-describedby="basic-addon2" v-if="!newCardHeader" v-model="cardTitle">
					<div class="input-group-append">
						<button class="btn btn-outline-secondary" type="button" v-if="!newCardHeader" @click="newCardHeader = true"><i class="fas fa-times"></i></button>
					</div>
				</div>
			</div>
			<textarea name="" class="textarea" id="" cols="30" rows="5" v-model="noteText"></textarea>
		</b-modal>
	
		<div class="row mt-5">
			<div class="col">
			</div>
			<div class="col text-center justify-content-bottom align-self-end">
				<button type="button" class="btn btn-outline-success" title="Новая заметка" v-show="!showModal" @click="showModal = !showModal; noteText=''; cardTitle=''; newCardHeader=true"><i class="fas fa-plus"></i></button>
			</div>
			<div class="col">
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "AddNote",
		data() {
			return {
				showModal: false,
				noteText: "",
				cardTitle: "",
				newCardHeader: true,
				newMassCard: []
			};
		},
		methods: {
			saveNote: function(cardTitle, noteText) {
				if (cardTitle === "") {
					cardTitle = "Новая заметка"
				}
				this.newMassCard.push({
					cardHeader: cardTitle,
					cardText: noteText
				})
				this.$emit('saveNote', {
					newNote: this.newMassCard
				})
			}
		}
	};
</script>

